<template>
    <div class="Assets mb-60">
        <MyMenu />
        <a-row class="t-left">
            <a-col :span="12" :offset="6">
                <a-row type="flex" style="display:none;">
                    <a-statistic title="Status" value="Pending" />
                    <a-statistic title="Price" prefix="$" :value="568.08" :style="{margin: '0 32px',}" />
                    <a-statistic title="Balance" prefix="$" :value="3345.08" />
                </a-row>

                <a-row :gutter="16" style="display:none;">
                    <a-col :span="12">
                        <a-card>
                            <a-statistic title="Feedback" :value="11.28" :precision="2" suffix="%" :value-style="{ color: '#3f8600' }" style="margin-right: 50px">
                                <template #prefix>
                                    <a-icon type="arrow-up" />
                                </template>
                            </a-statistic>
                        </a-card>
                    </a-col>
                    <a-col :span="12">
                        <a-card>
                            <a-statistic title="Idle" :value="9.3" :precision="2" suffix="%" class="demo-class" :value-style="{ color: '#cf1322' }">
                                <template #prefix>
                                    <a-icon type="arrow-down" />
                                </template>
                            </a-statistic>
                        </a-card>
                    </a-col>
                </a-row>

                <!-- Start -->
                <a-row>
                    <a-col>
                        <!-- Total Assets -->
                        <a-card>
                            <h3>Total Assets : 123456 USD</h3>
                        </a-card>

                    </a-col>
                </a-row>

                <a-row>
                    <!-- BTC -->
                    <a-card>
                        <a-row class="t-left">
                            <h4>BTC</h4>
                        </a-row>
                        <a-row>
                            <a-col :span="12" class="t-left">
                                123 BTC ≈ 456 USD
                            </a-col>
                            <a-col :span="12" class="t-right">
                                <a-button-group>
                                    <a-button @click="onClickShowWalletAddress('btc')" type="primary" ghost>
                                        Recharge
                                    </a-button>
                                    <a-button @click="onClickWithdraw" type="danger" ghost>
                                        Withdraw
                                    </a-button>
                                </a-button-group>
                            </a-col>
                        </a-row>

                        <a-row v-if="is_show_wallet_address=='btc'">
                            <h5>Wallet Address</h5>
                            <div id="qrcode"></div>
                            <a-row type="flex">
                                <a-col flex="auto">
                                    <a-input id="wallet_address" :value='wallet_address' placeholder="wallet address" read-only />
                                </a-col>
                                <a-col flex="60px">
                                    <a-button @click="onClickCopy()" type="primary" ghost>COPY</a-button>
                                </a-col>
                            </a-row>
                        </a-row>
                    </a-card>

                    <!-- ETH -->
                    <a-card>
                        <a-row class="t-left">
                            <h4>ETH</h4>
                        </a-row>
                        <a-row>
                            <a-col :span="12" class="t-left">
                                123 ETH ≈ 456 USD
                            </a-col>
                            <a-col :span="12" class="t-right">
                                <a-button-group>
                                    <a-button @click="onClickShowWalletAddress('eth')" type="primary" ghost>
                                        Recharge
                                    </a-button>
                                    <a-button @click="onClickWithdraw" type="danger" ghost>
                                        Withdraw
                                    </a-button>
                                </a-button-group>

                            </a-col>
                        </a-row>

                        <a-row v-if="is_show_wallet_address=='eth'">
                            <h5>Wallet Address</h5>
                            <div id="qrcode"></div>
                            <a-row type="flex">
                                <a-col flex="auto">
                                    <a-input id="wallet_address" :value='wallet_address' placeholder="wallet address" read-only />
                                </a-col>
                                <a-col flex="60px">
                                    <a-button @click="onClickCopy()" type="primary" ghost>COPY</a-button>
                                </a-col>
                            </a-row>
                        </a-row>
                    </a-card>

                    <!-- USDT -->
                    <a-card>
                        <a-row class="t-left">
                            <h4>USDT</h4>
                        </a-row>
                        <a-row>
                            <a-col :span="12" class="t-left">
                                123 USDT ≈ 456 USD
                            </a-col>
                            <a-col :span="12" class="t-right">
                                <a-button-group>
                                    <a-button @click="onClickShowWalletAddress('usdt')" type="primary" ghost>
                                        Recharge
                                    </a-button>
                                    <a-button @click="onClickWithdraw" type="danger" ghost>
                                        Withdraw
                                    </a-button>
                                </a-button-group>

                            </a-col>
                        </a-row>

                        <a-row v-if="is_show_wallet_address=='usdt'">
                            <a-radio-group v-model="usdt_type">
                                <a-radio-button value="trc20">
                                    TRC-20
                                </a-radio-button>
                                <a-radio-button value="erc20">
                                    ERC-20
                                </a-radio-button>
                            </a-radio-group>
                            <h5>Wallet Address</h5>
                            <div id="qrcode"></div>
                            <a-row type="flex">
                                <a-col flex="auto">
                                    <a-input id="wallet_address" :value='wallet_address' placeholder="wallet address" read-only />
                                </a-col>
                                <a-col flex="60px">
                                    <a-button @click="onClickCopy()" type="primary" ghost>COPY</a-button>
                                </a-col>
                            </a-row>
                        </a-row>
                    </a-card>

                    <!-- LETH -->
                    <a-card>
                        <a-row class="t-left">
                            <h4>LETH</h4>
                        </a-row>
                        <a-row>
                            <a-col :span="12" class="t-left">
                                123 LETH ≈ 456 USD
                            </a-col>
                            <a-col :span="12" class="t-right">
                                <a-button-group>
                                    <a-button @click="onClickWithdraw" type="danger" ghost>
                                        Withdraw
                                    </a-button>
                                </a-button-group>

                            </a-col>
                        </a-row>
                    </a-card>

                </a-row>
            </a-col>
        </a-row>

    </div>
</template>


<script>
import MyMenu from "@/components/MyMenu.vue";
export default {
    name: "Assets",
    components: {
        MyMenu,
    },
    data() {
        return {
            wallet_address: "1P46orWf7cCveV6n8cRqhNncBYLd1ZxknT",
            is_show_wallet_address: "",
            usdt_type: "trc20",
        };
    },
    methods: {
        onClickShowWalletAddress(symbol) {
            if (this.is_show_wallet_address == symbol) {
                this.is_show_wallet_address = "";
                return;
            }
            this.is_show_wallet_address = symbol;
            setTimeout(() => {
                this.initQR();
            }, 10);
        },
        onClickCopy() {
            $("#wallet_address").attr("readonly", "");
            $("#wallet_address").val();
            $("#wallet_address").select();
            document.execCommand("copy");
            alert("Copy Successful ：\n" + $("#wallet_address").val());
            $("#wallet_address").attr("readonly", "readonly");
        },
        onClickWithdraw() {
            this.$router.push("/my/withdraw");
        },
        initQR() {
            jQuery("#qrcode").qrcode({
                width: 128,
                height: 128,
                text: this.wallet_address,
            });
            $("#qrcode > canvas").on("click", () => {
                this.downloadCanvasImamge(
                    "canvas",
                    "qrcode-" + this.wallet_address + ".png"
                );
            });
        },
        init() {},
    },
    mounted() {
        this.init();
    },
};
</script>



